import React, {Component} from 'react';
import { Element } from 'react-scroll';
import axios from 'axios';

const defaultState = {
  name: "",
  phoneNumber: "",
  email: "",
  content: "",
  success: null,
  error: null
};

export default class Contact extends Component {
  constructor(){
    super();

    this.state = {...defaultState}
  }

  sendEmail(){
    const {name, phoneNumber, email, content} = this.state;

    axios.post('https://cfbwmp3p41.execute-api.us-east-1.amazonaws.com/dev/email/send',
      { name, phoneNumber, email, content },
      { headers: {'Content-Type': 'application/json'}
    })
      .then((response) => {
        this.setState({...defaultState, success: "Сообщение успешно отправлено"}, () => {
          setTimeout(() => this.setState({success: null}), 3000)
        })
    })
      .catch((error) => {
        this.setState({error: "Не удалось отправить сообщение, воспользуйтесь номером телефоном/почтой внизу страницы."}, () => {
          setTimeout(() => this.setState({error: null}), 3000)
        })
      });
  }

  render() {
    return (
      <Element name={"contact"}>
        <div className="section-padding section-2-bg" id="contact">
          <div className="container">
            <div className="row text-center heading-div-1">
              <h2>Контактная Форма</h2>
              <div className="underline"/>
            </div>
            <div className="row">
              <div className="col-sm-3 contact-img" id="canvas1">
              </div>
              <div className="col-sm-6 ">
                <div className="form-col-1 no-padding">
                  <div className="contact-form-1">
                    <div className="form-div-1">
                      <input className="form-input" type="text" name="name" placeholder="Введите Ваше Имя"
                             required autoComplete="off"
                             value={this.state.name}
                             onChange={({target}) => this.setState({name: target.value})}/>
                    </div>
                    <div className="form-div-1">
                      <input className="form-input" type="email" name="email" placeholder="Введите Ваш e-mail"
                             required autoComplete="off"
                             value={this.state.email}
                             onChange={({target}) => this.setState({email: target.value})}/>
                    </div>
                    <div className="form-div-1">
                      <input className="form-input" type="text" name="phone" placeholder="Введите Ваш номер телефона"
                             required autoComplete="off"
                             value={this.state.phoneNumber}
                             onChange={({target}) => this.setState({phoneNumber: target.value})}/>
                    </div>
                    <div className="form-div-1">

                      <textarea className="form-message" name="message" cols="40" rows="3"
                                placeholder="Введите сообщение" required autoComplete="off"
                                value={this.state.content}
                                onChange={({target}) => this.setState({content: target.value})}/>
                    </div>
                    <div className="button form-btn text-right">
                      <button type="submit" className="btn btn-1 submit" onClick={this.sendEmail.bind(this)}>Отправить</button>
                    </div>
                    <div className="messageDiv">
                      {this.state.success && <span className="sucessMessage">{this.state.success}</span>}
                      {this.state.error && <span className="failMessage">{this.state.error}</span>}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Element>
    )
  }
}
